import { FiPlusSquare } from "react-icons/fi";
import RedeemCard from "./RedeemCard";
import { motion } from "framer-motion";
import { useNavigate } from "react-router-dom";
import GiftCardList from "./GiftCardList";

const GiftCard = () => {
  const navigate = useNavigate();

  return (
    <motion.main
      className="flex-1"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.5 }}
    >
      <div className="bg-white p-6 rounded-md lg:shadow-md">
        <h2 className="text-2xl font-semibold mb-6">Gift Cards</h2>

        <GiftCardList />

        <button
          onClick={() => navigate("create")}
          className="px-4 py-2 flex justify-start items-center gap-2 border border-primeRed rounded-md text-primeRed bg-white hover:bg-offWhite"
        >
          <FiPlusSquare size={18} />
          <p className="font-medium">Create Gift Card</p>
        </button>

        <RedeemCard />
      </div>
    </motion.main>
  );
};

export default GiftCard;
