import Button from "../../components/Button";
import InputBox from "../../components/InputBox";
import useForm from "../../hooks/useForm";
import { useRedeemGiftCard } from "../../services/giftCardServices";
import { useToast } from "../../contexts/toastContext";
import { useWallet } from "../../contexts/walletContext";

const RedeemCard = () => {
  const toast = useToast();
  const { getBalance } = useWallet();
  const redeemMutation = useRedeemGiftCard();

  const onSubmitHandler = (data) => {
    const { code } = data;

    // Client side validations
    if (!code) {
      toast.error("Please enter a code");
      return;
    }

    redeemMutation.mutate(code, {
      onSuccess: async () => {
        await getBalance();
        toast.success("Balance added into wallet successfully");
        clearForm();
      },
      onError: (error) => {
        console.error(error);
        toast.error(error?.response?.data?.message);
      },
    });
  };

  const { formData, handleChange, handleSubmit, clearForm } = useForm(
    {},
    onSubmitHandler
  );

  return (
    <div className="mt-8">
      <form
        onSubmit={handleSubmit}
        className="flex flex-wrap items-end gap-x-8 gap-y-2"
      >
        <InputBox
          label="redeem gift card"
          name="code"
          value={formData.code}
          onChange={handleChange}
          placeholder="enter your gift code"
          className="w-60 h-10 border-2 border-secondary rounded-md px-4 py-2 text-sm placeholder:text-sm"
        />
        <Button
          disabled={redeemMutation.isPending}
          className="h-10 px-4 py-2 rounded-md"
        >
          redeem
        </Button>
      </form>
    </div>
  );
};

export default RedeemCard;
