import Spinner from "../../components/Spinner";
import { useGiftCards } from "../../services/giftCardServices";

const GiftCardList = () => {
  const { data: giftCards, isLoading, error } = useGiftCards();

  if (isLoading) {
    return (
      <div className="flex justify-center">
        <Spinner />
      </div>
    );
  }

  if (error) {
    return (
      <div className="flex justify-center text-sm text-error">
        {error.message}
      </div>
    );
  }

  return (
    <div className="flex flex-wrap justify-center gap-4 mb-8">
      {giftCards.length === 0 ? (
        <p className="text-center text-primary">No gift cards available.</p>
      ) : (
        giftCards.map((card) => (
          <div key={card._id} className="h-24 w-32 rounded overflow-hidden">
            <img
              src={card?.templateId?.template}
              alt={card?.templateId?.title}
              className="size-full object-cover object-center"
            />
          </div>
        ))
      )}
    </div>
  );
};

export default GiftCardList;
